<template>
   <div class="shopDiv" style="height: 100%">
     <van-tabs v-model="activeTabs" @change="changes" :color="borderColor" title-inactive-color="#484848" title-active-color="#FF8D59" swipeable>
       <van-tab  title="我的门店">
         <shopLeft v-if="shopShow"></shopLeft>
       </van-tab>
       <van-tab title="下级代理商"  v-if="tabShow">
         <shopRight v-if="!shopShow"></shopRight>
       </van-tab>
     </van-tabs>
   </div>
</template>

<script>
  const shopLeft = () => import("../../dataStat/shop/shopLeft.vue");
  const shopRight = () => import("../../dataStat/shop/shopRight.vue");

  export default {
    components:{
      shopLeft,
      shopRight
    },
      data() {
        return {
          activeTabs: 0,
          shopShow:true,
          tabShow:false,
          borderColor:"rgba(0,0,0,0)"
        }
      },
    // computed:{
    //    nvb(){
    //        return this.$store.state.agentStore
    //    }
    // },
    // watch:{
    //   nvb(agentStore){
    //     if(this.$store.state.accountType=="1"&&this.$store.state.agentStore==true){
    //       this.borderColor="rgb(255, 141, 89)";
    //       this.tabShow=true;
    //     }
    //   }
    // },
      mounted(){
        if(this.$store.state.accountType=="1"&&parseInt(this.$store.state.childAgentCount)>0){
          this.borderColor="rgb(255, 141, 89)";
          this.tabShow=true;
        }
      },
    beforeCreate () {
       window.document.body
    },
      methods:{
          changes(index) {
            if (index == 1) {
              this.shopShow = false;
            } else {
              this.shopShow = true;
            }
          }
        },
    }
</script>

<style scoped>

</style>



